import React from 'react'
import Item from '../Item'
import Details from '../Details'


const Main = ({
  coinsData,
  getCoinDetails,
  isShowDetails,
  isShowCoinDetails,
  isHideCoinDetails,
  selectCoinID
}) => {

  let coinNumber = 0




  return (
    <React.Fragment>
      {isShowDetails ?
        <div>
          <ul>
            {coinsData.length !== 0 ?
              coinsData.map((dataItem, key) => {
                

                const coinProps = {
                  coinNumber: coinNumber++,
                  isShowCoinDetails,
                  getCoinDetails,
                  dataItem
                }
                return (
                  <React.Fragment key={key}>
                    <Item {...coinProps} />
                  </React.Fragment>
                )
              })
              : <h1>111222</h1>
            }
          </ul>
        </div>
        :
        <div>
          <button onClick={()=>isHideCoinDetails()}>ok</button>
          {coinsData.length !== 0 ?
            coinsData
              .filter(dataItem => {
                if (selectCoinID !== '') {
                  return dataItem.CoinInfo.Id === selectCoinID
                }

                return dataItem
              })
              .map((dataItem, key) => {
                return (
                    <Details key={key} coinNumber={coinNumber} dataItem={dataItem} />
                )
              })
            : <h1>111222</h1>
          }
        </div>
      }
    </React.Fragment>
  )
}

export default Main